import { Component, OnInit, Directive } from '@angular/core';
import { MapData } from './mapData';
import { MapDataService } from './mapData.sevice';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [MapDataService]
})
@Directive({ selector: '[d]' })
export class AppComponent implements OnInit{
  title = 'Итоги выборов депутатов Законодательного Собрания Краснодарского края 2017';
  data: MapData[];
  selectedArea: MapData;
  showDetail= true;
  mainMap = true;
  popup=false;


  constructor(private mapDataService: MapDataService) { }

  getData(): void {
    this.mapDataService.getMapData().then(data => this.data = data)
  }

  ngOnInit(): void {
    this.getData();
  }
  onSelect(dataItem: MapData): void {
    this.selectedArea = dataItem;
    this.showDetail = true;
  }

  onClosed(isShow){
    this.showDetail=isShow;
    this.popup=false;
  }

  changeMap(triger){
    this.mainMap=triger;
    this.popup=false;
  }

  showPopup(trigger){
    this.popup=trigger;
  }

  select($event){
    var el=document.getElementsByTagName('path');
    for(var i=0; i<el.length; i++){
      el[i].classList.remove('active');
    }

    $event.target.classList.add('active');
  }

}





